
.home .navbar {
  border-radius: 0px;
}
.home .navbar-brand {
  margin-left: 30px;
  letter-spacing: 1px;
}
.home .navbar-brand:hover {
  color: #fff;
}
.navbar-form button {
  margin-top: 5px;
  padding: 6px 20px;
  letter-spacing: 1px;
  font-weight: 300;
}
.navbar-form .btn-group {
  margin-top: 5px;
}
.navbar-form .btn-group .btn {
  padding: 6px 20px;
}
.headers h5 {
  letter-spacing: 1px;
  font-weight: 300;
}
.home .center-postition {
  position: absolute;
  top: 50%;
  height: 100px;
  margin-top: 100%;
}
.modal-title {
  letter-spacing: 1px;
  font-weight: 800;
}
.form-group label {
  font-weight: 300;
  letter-spacing: .5px;
  font-size: 14px;
}
.form-group .btn-cancel {
  margin-right: 10px;
}
.modal-body p {
  letter-spacing: 1px;
  font-weight: 300;
}



